import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import NotFound from './Telas/NotFound.jsx';
import Produto from './Telas/Produto/ProdutoComponent.jsx';
import Pedido from './Telas/Pedido/PedidoComponent.jsx';
import PedidoProduto from './Telas/PedidoProduto/PedidoProdutoComponent.jsx';
import axios from 'axios';
import axiosRateLimit from 'axios-rate-limit';

// Configura o axios para limitar as solicitaes a 10 por minuto
const http = axiosRateLimit(axios.create(), { maxRequests: 10, perMilliseconds: 60000 });

function ScrollToTop() {
  const { pathname } = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [pathname]);

  return null;
}

function App() {
  return (
    <div>
      <Router>
        <ScrollToTop />
        <Routes>
          <Route path="*" element={<NotFound />} />
          <Route exact path="/" element={<><Produto />< Pedido />  <PedidoProduto /> </>} />
        </Routes>
      </Router>
    </div>
  );
}

export default App;
